import { Component } from 'inferno';
import RootContainer from './RootContainer';
import FormCard from './FormCard';
import FormCardImage from './FormCardImage';
import ScrollHint from './ScrollHint';
import FormCardContent from './FormCardContent';
import FormCardTitle from './FormCardTitle';
import ItcLink from './ItcLink';
import FormCardFields from './FormCardFields';
import FieldLabel from './FieldLabel';
import TypeFieldSelect from './TypeFieldSelect';
import Select from './Select';
import SelectFaculty from './SelectFaculty';
import Textarea from './TypeFieldTextArea';
import TextInput from './TextInput';
import PhoneInput from './PhoneInput';
import Submit from './Submit';

const App = () => (
  <RootContainer>
    <FormCard>
      <FormCardImage>
        <ScrollHint />
      </FormCardImage>

      <FormCardContent
        id="form"
        name="form-sign-up"
        action="/missrea2018"
        method="post"
        enctype="multipart/form-data"
      >
        <FormCardTitle>
          <span></span>
          <ItcLink href="https://vk.com/reu_itc" target="_blank">
            <img
              width="20px"
              src={`${process.env.PUBLIC_URL}/img/stamps.svg`}
            />
          </ItcLink>
        </FormCardTitle>
        <FormCardFields>
          <div>
            <FieldLabel required for="fio">
              
            </FieldLabel>
            <TextInput id="fio" type="text" name="fio" maxlength="256" />
          </div>
          <Select>
            <TypeFieldSelect name="fakultet">
              <option value="unset" selected style="display:none;">
                *
              </option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=" "> </option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value="Integral">Integral</option>
            </TypeFieldSelect>
          </Select>
          <Select>
            <TypeFieldSelect required name="kurs">
              <option value="unset" selected style="display:none;">
                *
              </option>
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
              <option value="5">5</option>
              <option value="1 ">1, </option>
              <option value="2 ">2, </option>
            </TypeFieldSelect>
          </Select>
          <FieldLabel required for="group">
            
          </FieldLabel>
          <TextInput id="group" type="text" name="group" maxlength="256" />
          <FieldLabel required for="phone">
            
          </FieldLabel>
          <PhoneInput
            id="phone"
            type="tel"
            name="phone"
          />
          <FieldLabel required for="vkLink">
             
          </FieldLabel>
          <TextInput id="vkLink" type="url" name="vkLink" maxlength="256" />

          <FieldLabel required for="experience">
             
          </FieldLabel>
          <Textarea
            id="experience"
            rows="1"
            cols="5"
            name="experience"
            placeholder=" , , "
            maxlength="1024"
          />
          <FieldLabel required for="otherExperience">
              
          </FieldLabel>
          <Textarea
            id="otherExperience"
            rows="1"
            cols="5"
            name="otherExperience"
            placeholder=",    .."
            maxlength="1024"
          />
          <FieldLabel required for="inventoryAvailiable">
             
          </FieldLabel>
          <Textarea
            id="inventoryAvailiable"
            rows="1"
            cols="5"
            name="inventoryAvailiable"
            placeholder=", ,   .."
            maxlength="1024"
          />
          <Select>
          <TypeFieldSelect required name="fitnessLevel">
            <option value="unset" selected style="display:none;">
              *   (1-10)
            </option>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
            <option value="5">5</option>
            <option value="6">6</option>
            <option value="7">7</option>
            <option value="8">8</option>
            <option value="9">9</option>
            <option value="10">10</option>

          </TypeFieldSelect>
        </Select>
          <FieldLabel required for="diseases">
              
          </FieldLabel>
          <Textarea
            id="diseases"
            rows="1"
            cols="5"
            name="diseases"
            maxlength="1024"
          />
          <FieldLabel required for="allergies">
            
          </FieldLabel>
          <Textarea
            id="allergies"
            rows="1"
            cols="5"
            name="allergies"
            maxlength="1024"
          />
          <FieldLabel required for="yourTrip">
               
          </FieldLabel>
          <Textarea
            id="yourTrip"
            rows="1"
            cols="5"
            name="yourTrip"
            maxlength="1024"
          />
          <Submit type="submit" name="submit"></Submit>

        </FormCardFields>
      </FormCardContent>
    </FormCard>
  </RootContainer>
);

export default App;



